"use client";

import Image from "next/image";
import Link from "next/link";
import { useRouter, useSearchParams } from "next/navigation";
import { useState } from "react";

export default function LoginPage() {
  const router = useRouter();
  const sp = useSearchParams();
  const nextUrl = sp.get("next") || "/account";

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPw, setShowPw] = useState(false);
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState<string | null>(null);

  async function onSubmit(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    setErr(null);

    const res = await fetch("/api/auth/login", {
      method: "POST",
      body: JSON.stringify({ email, password }),
    });

    if (!res.ok) {
      const data = await res.json().catch(() => ({}));
      setErr(data?.detail || "–ù–µ–≤–µ—Ä–Ω—ã–π e-mail –∏–ª–∏ –ø–∞—Ä–æ–ª—å.");
      setLoading(false);
      return;
    }
    router.replace(nextUrl);
  }

  return (
    <main className="min-h-dvh">
      <section className="mx-auto w-full max-w-6xl px-4 md:px-6 lg:px-8 py-8 md:py-14">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-10 items-start">
          {/* Left: photo */}
          <div className="relative w-full overflow-hidden rounded-2xl bg-neutral-100 aspect-[4/3] md:h-[560px]">
            <Image
              src="/images/auth/login.jpg" // <-- replace with your image
              alt="Nely Look ‚Äî mood"
              fill
              className="object-cover object-center"
              priority
            />
          </div>

          {/* Right: form */}
          <div className="w-full md:max-w-[520px] md:ml-auto">
            {/* Brand lockup (optional if header already shows logo) */}
            <div className="mb-6 text-center md:text-left">
              <div className="flex items-baseline justify-center md:justify-start">
                <span className="text-[28px] font-semibold tracking-wide text-[#7a2b8a]">NELY</span>
                <span className="ml-2 text-sm tracking-[0.35em] text-[#7a2b8a]">LOOK</span>
              </div>
            </div>

            <h1 className="text-2xl md:text-3xl font-semibold">
              –î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å –≤ Nely Look!
            </h1>
            <p className="mt-1 text-sm text-neutral-600">
              –í–æ–π–¥–∏—Ç–µ –¥–ª—è –ª—É—á—à–µ–≥–æ —à–æ–ø–ø–∏–Ω–≥–∞ –Ω–∞ Nely Look
            </p>

            <form onSubmit={onSubmit} className="mt-6 space-y-4">
              <div>
                <label className="mb-1 block text-sm font-medium">–í–∞—à Email</label>
                <input
                  className="w-full rounded-lg border px-3 py-2 outline-none focus:ring-2 focus:ring-black/70"
                  type="email"
                  inputMode="email"
                  autoComplete="email"
                  placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à e-–º–µ–π–ª"
                  required
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>

              <div>
                <div className="mb-1 flex items-center justify-between">
                  <label className="text-sm font-medium">–ü–∞—Ä–æ–ª—å</label>
                  <Link href="/forgot-password" className="text-xs underline">
                    –ó–∞–±—ã–ª–∏ –ø–∞—Ä–æ–ª—å?
                  </Link>
                </div>
                <div className="relative">
                  <input
                    className="w-full rounded-lg border px-3 py-2 pr-10 outline-none focus:ring-2 focus:ring-black/70"
                    type={showPw ? "text" : "password"}
                    autoComplete="current-password"
                    placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                  <button
                    type="button"
                    aria-label={showPw ? "–°–∫—Ä—ã—Ç—å –ø–∞—Ä–æ–ª—å" : "–ü–æ–∫–∞–∑–∞—Ç—å –ø–∞—Ä–æ–ª—å"}
                    onClick={() => setShowPw((s) => !s)}
                    className="absolute inset-y-0 right-0 grid place-items-center px-3 text-neutral-500"
                  >
                    {showPw ? "üôà" : "üëÅÔ∏è"}
                  </button>
                </div>
              </div>

              {err && <p className="text-sm text-red-600">{err}</p>}

              <button
                className="w-full rounded-lg bg-black py-2 text-white disabled:opacity-60"
                disabled={loading}
              >
                {loading ? "–í—Ö–æ–¥–∏–º‚Ä¶" : "–í–æ–π—Ç–∏"}
              </button>
            </form>

            {/* Divider */}
            <div className="my-6 flex items-center gap-3 text-xs text-neutral-500">
              <span className="h-px flex-1 bg-neutral-200" />
              –ò–ª–∏ –ø—Ä–æ–¥–æ–ª–∂–∏—Ç—å —á–µ—Ä–µ–∑
              <span className="h-px flex-1 bg-neutral-200" />
            </div>

            {/* Social (stub for now) */}
            <button
              className="flex w-full items-center justify-center gap-3 rounded-lg border px-3 py-2"
              type="button"
              onClick={() => alert("–ü–æ–¥–∫–ª—é—á–∏—Ç–µ Google OAuth")}
            >
              <svg width="18" height="18" viewBox="0 0 24 24" aria-hidden="true">
                <path
                  d="M21.35 11.1H12v2.9h5.3c-.23 1.5-1.8 4.4-5.3 4.4A6.3 6.3 0 1 1 17.7 7.9l2.06-2.06A9.05 9.05 0 1 0 12 21a9 9 0 0 0 9.35-9.9Z"
                  fill="currentColor"
                />
              </svg>
              Google
            </button>

            <p className="mt-6 text-center text-sm">
              –ï—â—ë –Ω–µ—Ç –ø—Ä–æ—Ñ–∏–ª—è Nely Look?{" "}
              <Link href="/register" className="font-medium underline">
                –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
              </Link>
            </p>

            <p className="mt-2 text-center text-xs">
              <Link href="/send-verification" className="underline">
                –û—Ç–ø—Ä–∞–≤–∏—Ç—å –ø–∏—Å—å–º–æ –¥–ª—è –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è
              </Link>
            </p>
          </div>
        </div>
      </section>
    </main>
  );
}
